.ContactEditor {
  display: flex;
  flex-direction: column;
  padding: 12px;
  width: 400px;
  border-radius: 2px;
  box-shadow: 0px 3px 3px 0px rgba(0, 0, 0, 0.2);
  margin-bottom: 16px;
}

.ContactEditor-label {
  display: block;
  margin-bottom: 16px;
  font-size: 20px;
  color: #171718;
  cursor: pointer;
}

.ContactEditor-input {
  color: #171718;
  font: inherit;
  font-size: 1.2rem;
  padding: 10px;
  border-radius: 4px;
  border: 1px solid #bdbdbd;
  width: 90%;
}

.ContactEditor-input:focus {
  border: 1px solid #146e8a;
}

.ContactEditor-button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  background-color: rgb(18, 90, 150);
  padding: 8px 24px;
  border-radius: 4px;
  border: 1px solid #1ebd65;
  cursor: pointer;
  text-transform: uppercase;
}

.ContactEditor-button:hover {
  background-color: #2296b9;
}
/*Animation*/
.ContactList-item-enter {
  transform: translateX(-100%);
}
.ContactList-item-enter-active {
  transform: translateX(0);
  transition: all 250ms linear;
}

.ContactList-item-exit {
  transform: translateX(0);
}

.ContactList-item-exit-active {
  transform: translateX(-100%);
  transition: all 250ms linear;
}

/*Basic styles*/
.ContactList {
  width: 400px;
  margin: 0;
}

.ContactList-item {
  display: flex;
  align-items: center;
  padding: 12px;
  display: flex;
  justify-content: space-between;
}

.ContactList-text {
  flex: 0 0 85%;
  margin-top: 0;
  margin-bottom: 0;
  margin-right: 16px;
  border-radius: 3px;
}

.ContactList-button {
  margin: 0 4px;
  background-color:  rgb(18, 90, 150);
  border: 1px solid #1ebd65;
  color: #fff;
  border-radius: 4px;
}

.ContactList-button:hover {
  background-color: #2296b9;
  cursor: pointer;
}

/* Animation */
.alert-enter {
  transform: translateX(100%);
}
.alert-enter-active {
  transform: translateX(0);
  transition: all 250ms linear;
}
.alert-exit {
  transform: translateX(0);
}
.alert-exit-active {
  transform: translateX(100%);
  transition: all 250ms linear;
}

/*Basic styles*/
.alert {
  margin: 20px 20px;
  width: 200px;
  height: 40px;
  background-color: rgb(228, 97, 21);
  border-radius: 6px;
  padding: 10px 42px;
  box-shadow: 0px 2px 3px 0px rgba(0, 0, 0, 0.4);
}
.alert-message {
  margin: 0 auto;
  color: rgb(255, 255, 255);
  font-size: 20px;
}
/*Animation*/
.fade-appear {
  transform: translateX(-100%);
}

.fade-appear-active {
  transform: translateX(0);
  transition: all 500ms linear;
}

/*Basic styles*/
.wrapper {
  display: block;
}
.header {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.logo {
  font-size: 40px;
  color: rgb(18, 90, 150);
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

